.about {
    background-color: #fff5f6;
    padding-top: 125px;
    padding-bottom: 39px;
    position: relative;
    background-image: url('/src/images/mobile/bg-milk.webp');
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: 70% 90%;
  
    &__lable {
      font-family: Titan One, sans-serif;
      color: #ffa5ba;
      font-size: 18px;
      font-weight: 400;
      font-style: normal;
      line-height: 1.17;
      align-items: center;
      text-align: center;
      text-transform: uppercase;
      margin-bottom: 15px;
      cursor: default;
      transform: scale(1);
  
      &:hover,
      &:focus {
        transform: scale(2);
        transition: all linear 300ms;
      }
    }
  }
  
  .about__title {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 39px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #d41443;
    transform: scale(1);
  
    &:hover,
    &:focus {
      transform: scale(1.5);
      transition: all linear 300ms;
    }
  }
  .about__image {
    width: 280px;
    height: 279px;
  
    &:hover {
      animation-name: spin;
      animation-duration: 5s;
      animation-timing-function: linear;
      animation-iteration-count: 1;
    }
  }
  .about__pharagraph {
    padding: 0 30px;
    &-one {
      font-family: 'DM Sans';
      color: #000000;
      font-size: 12px;
      font-weight: 500;
      font-style: normal;
      line-height: 1.58;
      margin-top: 46px;
      margin-bottom: 27px;
      cursor: pointer;
  
      &:hover,
      &:focus {
        color: #d41443;
        transition: all linear 250ms;
      }
    }
    &-two {
      font-family: 'DM Sans';
      color: #907e82;
      font-size: 12px;
      font-weight: 500;
      font-style: normal;
      line-height: 1.58;
      cursor: default;
      transform: scale(1);
      transition: all linear 250ms;
      margin-bottom: 10px;
  
      &:hover {
        transform: scale(1.2);
      }
    }
  }
  
  @media screen and (min-width: 768px) {
    .about {
      background-image: none;
    }
    .about__main {
      display: flex;
    }
    .about__main-image {
      width: 340px;
      height: 339px;
    }
    .about__pharagraph {
      display: block;
      padding: 0;
      margin-left: 17px;
      width: 340px;
      z-index: 1;
    }
    .about__pharagraph-one {
      font-size: 14px;
      line-height: 1.79;
      margin-top: 50px;
      margin-bottom: 19px;
    }
    .about__pharagraph-two {
      font-size: 14px;
      line-height: 1.79;
      margin-bottom: 9px;
    }
  }
  
  @media screen and (min-width: 1280px) {
    .about {
      padding-top: 125px;
      padding-bottom: 39px;
  
      background-image: url('/src/images/desktop/bg-milk.webp');
      background-position: 70% 140%;
    }
    .about__main {
      display: flex;
    }
    .about__main-image {
      width: 516px;
      height: 516px;
    }
    .about__image {
      height: 516px;
      width: 516px;
      margin-left: 100px;
      transform: scale(1);
      &:hover {
        transform: scale(1.2);
        transition: all linear 300ms;
      }
    }
    .about__pharagraph {
      display: block;
      padding-top: 80px;
      margin-left: 200px;
      padding-left: 200px;
      z-index: 1;
      width: 600px;
    }
  
    .about__pharagraph-one {
      font-size: 16px;
      line-height: 1.75;
      margin-top: 4px;
      margin-bottom: 35px;
    }
    .about__pharagraph-two {
      margin-bottom: 35px;
    }
  }
  
  .about__main-image {
    width: 280px;
    height: 280px;
  }
  .about__button {
    position: relative;
    display: block;
    width: 176px;
    height: 44px;
    background-color: #fff;
    border-radius: 30px;
    border: 0px;
    font-family: 16px;
    color: #d41443;
    font-size: 14px;
    font-weight: 700;
    font-style: normal;
    line-height: 1.29;
    text-align: center;
    cursor: pointer;
  
    &:hover,
    &:focus {
      background-color: #fff;
      transform: scale(1.4);
    }
  }
  