.advantages {
    padding-bottom:50px;
   background-color:#FFF5F6;
    
    &__icon {
        animation: pulsate 2s infinite;
        transform-origin: center;

        @keyframes pulsate {
            0% {
              transform: scale(1);
            }
            50% {
              transform: scale(1.2);
            }
            100% {
              transform: scale(1);
            }
          }
        &--cow {
            width:33px;
            height:50px;
        }
        &--bucket {
            width:50px;
            height:50px;
        }

        &--heart {
            width:49px;
            height:50px;
        }
    }

    &__list {
        list-style: none;
        padding:0px;
        gap:20px;
    }

    &__item {
        padding-bottom:20px;
        padding-left:20px;
        display:flex;
        flex-direction:column;
    }

    &__number {
        font-family: 'Titan One';
        font-style: normal;
        font-weight: 400;
        font-size: 36px;
        line-height: 1.14;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: #D41443;
        padding-top:16px;
        padding-bottom:4px;

        }


    &__description {
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 12px;
        line-height: 1.70;
        letter-spacing: 0.02em;
        color: #000000;

    }
}

.visually-hidden {
    display:none;
}

@media screen and (min-width:480px) {
    .advantages {

        &__list {
            display:flex;
            flex-direction:row;
            padding-right:173px;
            padding-left:32px;
            gap:20px;
        }
    
        &__number {
            font-size:48px;
            line-height:1.145;
            padding-top:36px;
            padding-bottom:13px;
        }

        &__description {
            font-size:14px;
            line-height:1.74;
        }
                }
}

@media screen and (min-width:768px) {
    .advantages {
    &__list {
        display:flex;
        flex-direction:row;
        padding-right:96px;
        padding-left:16px;
        gap:90px;
    }

    &__number {
        font-size:58px;
        line-height:1.137;
        padding-top:40px;
        padding-bottom:16px;
    }

    &__description {
        font-size:16px;
        line-height:1.76;
    }
            }
}

