:root {
  --primary-font: "DM Sans", sans-serif;
  --secondary-font: "Titan One", cursive, sans-serif;
  --primary-text-color: #fff;
  --secondary-text-color: #000;
  --accent-color: #d41443;
  --info-color: #ffa5ba;
  --description-color: #907e82;
}

.visually-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.button {
  letter-spacing: .06em;
  color: var(--accentColor);
  background-color: var(--primary-text-color);
  cursor: pointer;
  border-style: none;
  border-radius: 22px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
}

.button:hover, .button:focus {
  background-color: #ecebeb;
}

.button--red {
